* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 18px;
  max-width: 100vw;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Fraunces', serif;
}

p, ul, li {
  font-family: 'Barlow', sans-serif;
}